.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;

    margin: 0;
    margin-left: 4px;

    background-image: url(/images/star-active.svg);
    background-repeat: no-repeat;
    background-position: center;

    &:first-child {
      margin-left: 0;
    }

    &:nth-child(n+5) {
      background-image: url(/images/star.svg);
    }
  }
}
