.main-block {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  max-width: 944px;

  margin: 0 auto;

  &__card {
    margin-top: 50px;

    &:first-child {
      margin-left: 0;
    }

    &:nth-child(5n) {
      margin-left: 0;
    }

    &:nth-child(n+5) {
      margin-top: 46px;
    }
  }
}
